import React from 'react';
import { node, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-cvv-information';

const InputCvvInformation = ({
  children,
  className,
  maxLength,
  ...remainingProps
}) => (
  <figure
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    <div
      className={classnames(`${CSS_PREFIX}__svg`, {
        [`${CSS_PREFIX}__svg--three-digit-img`]: parseInt(maxLength, 10) === 3,
        [`${CSS_PREFIX}__svg--four-digit-img`]: parseInt(maxLength, 10) === 4
      })}
    />
    <figcaption>
      {children}
    </figcaption>
  </figure>
);

InputCvvInformation.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Max Length
   */
  maxLength: oneOfType([number, string])
};

InputCvvInformation.defaultProps = {
  maxLength: 3
};

InputCvvInformation.displayName = 'InputCvvInformation';

export default InputCvvInformation;
