import React, { useState } from 'react';
import ButtonText from '../../button-text';
import Col from '../../col';
import Grid from '../../grid';
import InputCvv from '../../input-cvv';
import Row from '../../row';
import Typography from '../../typography';

const InputCvvExample = () => {
  const [stateOne, setStateOne] = useState({ value: '044' });
  const [stateTwo, setStateTwo] = useState({ maxLength: 3 });
  const [stateThree, setStateThree] = useState({ maxLength: 3 });

  return (
    <Grid>
      <Row>
        <Col>
          InputCvv Default Uncontrolled (3 digits)
        </Col>
      </Row>
      <Row>
        <Col>
          <InputCvv
            errorText="Enter 3 digits"
            name="cvv"
            showErrorText
          />
        </Col>
      </Row>
      <Row>
        <Col>
          InputCvv Default Controlled (3 digits)
        </Col>
      </Row>
      <Row>
        <Col>
          <InputCvv
            errorText="Enter 3 digits"
            name="cvv"
            onChange={e => setStateOne({ value: e.target.value })}
            showErrorText
            value={stateOne.value}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          InputCvv Default (4 digits)
        </Col>
      </Row>
      <Row>
        <Col>
          <InputCvv
            errorText="Enter 4 digits"
            maxLength={4}
            name="cvv"
            showErrorText
          />
        </Col>
      </Row>
      <Row>
        <Col>
          InputCvv disabled
        </Col>
      </Row>
      <Row>
        <Col>
          <InputCvv
            errorText="Enter 3 digits"
            disabled name="cvv"
            showErrorText
          />
        </Col>
      </Row>
      <Row>
        <Col>
          InputCvv readOnly
        </Col>
      </Row>
      <Row>
        <Col>
          <InputCvv
            errorText="Enter 3 digits"
            name="cvv"
            readOnly
            showErrorText
          />
        </Col>
      </Row>
      <Row>
        <Col>
          InputCvv error
        </Col>
      </Row>
      <Row>
        <Col>
          <InputCvv
            errorText="Enter 3 digits"
            invalid
            name="cvv"
            showErrorText
          />
        </Col>
      </Row>
      <Row>
        <Col>
          InputCvv Required with custom error text
        </Col>
      </Row>
      <Row>
        <Col>
          <InputCvv
            errorText="Input Required"
            name="cvv"
            required
            showErrorText
          />
        </Col>
      </Row>
      <Row>
        <Col>
          InputCvv Default (3 digits)

          Custom labels
        </Col>
      </Row>
      <Row>
        <Col>
          <InputCvv
            containerProps={{ lang: 'ja' }}
            errorText={`桁のコードを入力します。`}
            label={maxLength => `${maxLength}桁のコードを入力します。`}
            name="cvv"
            showErrorText
          >
            {
              maxLength => (
                <React.Fragment>
                  <Typography lang="ja" variant="subtitle">セキュリティコード</Typography>
                  <Typography lang="ja" variant="caption">カードの裏側にある{maxLength}桁のコード</Typography>
                </React.Fragment>
              )
            }
          </InputCvv>
        </Col>
      </Row>
      <Row>
        <Col>
          InputCvv Switch from 3 to 4 digits
        </Col>
      </Row>
      <Row>
        <Col>
          <InputCvv
            errorText={`Enter ${stateTwo.maxLength === 3 ? 3 : 4} digits`}
            maxLength={stateTwo.maxLength}
            name="cvv"
            showErrorText
          />
          <ButtonText
            onClick={() => setStateTwo({ maxLength: stateTwo.maxLength === 3 ? 4 : 3 })}
          >
            Switch to {stateTwo.maxLength === 3 ? 4 : 3} digits
          </ButtonText>
        </Col>
      </Row>
      <Row>
        <Col>
          InputCvv switch between 3  and 4 digits with custom labels
        </Col>
      </Row>
      <Row>
        <Col>
          <InputCvv
            containerProps={{ lang: 'ja' }}
            errorText={`桁のコードを入力します。`}
            label={maxLength => `${maxLength}桁のコードを入力します。`}
            name="cvv"
            maxLength={stateThree.maxLength}
            showErrorText
          >
            {
              maxLength => (
                <React.Fragment>
                  <Typography lang="ja" variant="subtitle">セキュリティコード</Typography>
                  <Typography lang="ja" variant="caption">カードの裏側にある{maxLength}桁のコード</Typography>
                </React.Fragment>
              )
            }
          </InputCvv>
          <ButtonText
            onClick={() => setStateThree({ maxLength: stateThree.maxLength === 3 ? 4 : 3 })}
          >
            Switch to {stateThree.maxLength === 3 ? 4 : 3} digits
          </ButtonText>
        </Col>
      </Row>
    </Grid>
  );
};

export default InputCvvExample;
