/* eslint-disable react/display-name */

import React, { forwardRef, Fragment, useEffect, useRef, useState } from 'react';
import { bool, func, node, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import shortid from 'shortid';
import ButtonIcon from '../button-icon';
import Icon from '../icon';
import Input from '../input';
import InputCvvInformation from '../input-cvv-information';
import InputOption from '../input-option';
import Typography from '../typography';

const CSS_PREFIX = 'vds-input-cvv';
const INPUT_TYPE = {
  PASSWORD: 'password',
  TEXT: 'text'
};

const InputCvv = forwardRef(({
  autoComplete,
  buttonAriaLabel,
  children,
  className,
  css,
  disabled,
  id = shortid.generate(),
  inputMode,
  label,
  name,
  maxLength,
  pattern,
  showInfoButton,
  ...remainingProps
}, ref) => {
  const [showCvvInfo, setShowCvvInfo] = useState(false);
  const [inputType, setInputType] = useState(INPUT_TYPE.PASSWORD);
  const uniqueId = useRef(id);
  const uid = uniqueId.current;
  ref = ref || useRef();

  const handleBlur = () => setInputType(INPUT_TYPE.PASSWORD);
  const handleClick = () => setShowCvvInfo(!showCvvInfo);
  const handleFocus = () => setInputType(INPUT_TYPE.TEXT);

  // Prevent pasting of non-digits
  const handlePaste = e => {
    e.stopPropagation();

    const clipboardData = e.clipboardData || window.clipboardData;
    const pastedText = clipboardData.getData('Text');

    if (!/^\d*$/.test(pastedText)) {
      e.preventDefault();
    }
  };

  // Prevent alpha letters
  // but allow numeric and combination keys
  const handleKeyDown = e => {
    if (
      e.key.length === 1 &&
      !/^\d*$/.test(e.key) &&
      !e.altKey &&
      !e.ctrlKey &&
      !e.metaKey &&
      !e.shiftKey
    ) {
      e.preventDefault();
    }
  };

  useEffect(() => {
    if (ref.current.value && (ref.current.value.length > maxLength)) {
      ref.current.value = ref.current.value.substr(0, maxLength);
    }
  }, [maxLength, ref]);

  return (
    <div
      className={classnames(CSS_PREFIX, className)}
      css={css}
    >
      <Input
        autoComplete={autoComplete}
        disabled={disabled}
        label={typeof label === 'function' ? label(maxLength) : label}
        id={uid}
        inputMode={inputMode}
        maxLength={maxLength}
        name={name}
        onBlur={handleBlur}
        onFocus={handleFocus}
        onKeyDown={handleKeyDown}
        onPaste={handlePaste}
        pattern={pattern ? pattern : `[0-9]{${maxLength}}`}
        ref={ref}
        type={inputType}
        {...remainingProps}
      >
        {
          showInfoButton && !disabled
            ? <InputOption>
              <ButtonIcon
                aria-controls={`${uid}__cvv-information`}
                aria-expanded={showCvvInfo.toString()}
                ariaLabel={buttonAriaLabel}
                iconType="light-tiny"
                onClick={handleClick}
              >
                <Icon name="info" resolution="low" />
              </ButtonIcon>
            </InputOption>
            : undefined
        }
      </Input>
      {
        showCvvInfo
          ? <InputCvvInformation id={`${uid}__cvv-information`} maxLength={maxLength}>
            {
              typeof children === 'function'
                ? children(maxLength)
                : children
            }
          </InputCvvInformation>
          : undefined
      }
    </div>
  );
});

InputCvv.propTypes = {
  /**
   * Auto Complete
   */
  autoComplete: string,

  /**
   * Button Aria Label
   */
  buttonAriaLabel: string,

  /**
  *  a Function that provides maxLength as param
  */
  children: oneOfType([func, node]),

  /**
   * @ignore
   */
  className: string,

  /**
   * @ignore
   */
  css: func,

  /**
   * If true, the input will be disabled.
   */
  disabled: bool,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Input Mode to force mobile screens to use large number pad
   */
  inputMode: string,

  /**
   * Label
   */
  label: oneOfType([func, string]),

  /**
   * Max Length
   */
  maxLength: number,

  /**
   * Name
   */
  name: string,

  /**
   * Pattern
   */
  pattern: string,

  /**
   * Show info button
   */
  showInfoButton: bool,

  /**
   * Style
   */
  style: string
};

InputCvv.defaultProps = {
  autoComplete: 'cc-csc',
  buttonAriaLabel: 'CVV Information',
  children: maxLength => (
    <Fragment>
      <Typography variant="subtitle">Security Code</Typography>
      <Typography variant="caption">The {maxLength}-digit code on the back of your card</Typography>
    </Fragment>
  ),
  inputMode: 'decimal',
  label: maxLength => `Enter ${maxLength} digit CVV`,
  maxLength: 3,
  name: 'cvc',
  showInfoButton: true
};

InputCvv.displayName = 'InputCvv';

export default InputCvv;
